@import url(https://fonts.googleapis.com/css?family=Fira+Sans&display=swap);
body {
    margin: 0px;
}

body, p, span, label, button, input {
    margin: 0px;
    padding: 0px;
    font-family: "Fira Sans";
    font-size: 16px;
}

h1 {
    font-size: 35px;
}

.overflow-scroll {
    overflow: scroll;
}

.center {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.full {
    width: 100%;
    height: 100%;
}

.dummy-height {
    height: 30px;
}

.button {
    padding: 2px 4px;
    border: 1px solid black;
    background: white;
    border-radius: 3px;
}

.rounded {
    border-radius: 50%;
    width: 25px;
    height: 25px;
    margin: 4px;
}

.normal-padding {
    padding: 8px;
}

.normal-page-padding {
    padding: 16px 40px;
}

.flex-container {
    display: flex;
    align-items: center;
    margin: 8px;
    padding: 0px;
    width: 100%;
}

.flex-expand {
    flex: 1 1;
}

.flex-fixed {
    flex: 0 0 auto;
}
.top-bar {
    position: fixed;
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    top: 0;
    width: 100vw;
    height: 30px;
    z-index: 1;
    background-color: white;
    padding: 16px;
}

.title {
    flex: 0 0 auto;
    display: inline-block;
    font-size: 25px;
    margin: 4px 16px;
}

.link {
    flex: 0 0 auto;
    display: inline-block;
    color: black;
    font-style: normal;
    margin: 8px;
}
.hidden {
    display: none;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
@media (orientation: landscape) {
    .square-container {
        height: 100vh;
        max-height: 100vh;
        display: flex;
        align-items: stretch;
        flex-direction: row;
    }
    
    .square-block {
        max-width: calc(100vh - 32px);
        padding: 16px;
        flex-grow: 1;
        flex-shrink: 1;
        flex-basis: calc(100vh - 32px);
    }
}

@media (orientation: portrait) {
    .square-container {
        display: flex;
        align-items: stretch;
        flex-direction: column;
    }
    
    .square-block {
        max-height: calc(100vw - 32px);
        padding: 16px;
        flex-grow: 1;
        flex-shrink: 1;
        flex-basis: calc(100vw - 32px);
    }
}

.square-other {
    flex: 0 0 auto;
}
.setting-name {
    flex-basis: 160px;
}

.setting-end {
    flex-basis: 100px;
}
.result-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.result-line {
    flex: 0 0 auto;
    display: flex;
    flex-direction: row;
}
.result-guide-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.result-guide-item {
    flex: 0 0 auto;
    
    display: flex;
    flex-direction: row;
    margin: 8px;
    align-items: center;
}
.container {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    box-sizing: border-box;
}

.left {
    display: flex;
    justify-content: left;
    align-items: center;
}

